.nav__contents{
    display: flex;
    justify-content: space-between;

}
.nav__logo{
    position: fixed;
    top: 20px;
    left: 0;
    width: 100px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
  
}
.nav__avatar{
    cursor: pointer;
    position: fixed;
    right: 20px;
    width: 40px;
  

}

.nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav__black{
    background-color: #111;
}
